* {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars-rating {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.stars-rating__review {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card-price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card-price__title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card-price__velue {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card-button {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card-button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}
